/* Transitions  */

.slide-vertical-enter {
    opacity: 0;
    transform: translateY(-20px);
  }
  
  .slide-vertical-enter-active {
    opacity: 1;
    transform: translateY(0);
    transition: all 500ms;
  }
  
  .slide-vertical-exit {
    opacity: 1;
    transform: translateY(0);
  }
  
  .slide-vertical-exit-active {
    opacity: 0;
    transition: all 500ms;
    transform: translateY(-20px);
  }